.form-wrapper {
  width: 100%;
  height: auto;
  padding: 80px 0;
  background-color:#0075A2;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}


form.form-contact {
  max-width: 640px;
  width: 90%;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

h3.contact-title{
  font-family: 'Nunito';
  color: #fff;
  font-size: 32px;
  font-weight: 100;
  text-align: center;
}


.input-form {
  width: 100%;
  height: auto;
  font-size: 24px;
  border: 0;
  padding: 25px 15px;
  background: none;
  border-top: 1px solid transparent;
  border-right: 1px solid transparent;
  border-left: 1px solid transparent;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  font-family: 'Nunito';
  font-weight: 100;
  outline: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 0;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}

.input-form.incorrect {
  border: 2px solid red;
}

.input-form::-webkit-input-placeholder {
  color: rgba(255,255,255,.3);
  font-family: 'Nunito';
  font-weight: 100;
}

.input-form:-ms-input-placeholder {
  color: rgba(255,255,255,.3);
  font-family: 'Nunito';
  font-weight: 100;
}

.input-form::-ms-input-placeholder {
  color: rgba(255,255,255,.3);
  font-family: 'Nunito';
  font-weight: 100;
}

.input-form::placeholder {
  color: rgba(255,255,255,.3);
  font-family: 'Nunito';
  font-weight: 100;
}

.input-form:focus {
  border: 1px solid #fff;
}

button.submit-contact {
  background: none;
  border: 1px solid #fff;
  padding: 15px 0;
  width: 100%;
  color: #fff;
  font-family: 'Nunito';
  font-size: 24px;
  font-weight: 100;
  margin-top: 15px;
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
  opacity: .3;
  cursor: pointer;
}

button.submit-contact.complete-form:hover,button.submit-contact.complete-form {
  background-color: #fff;
  color: #0075A2;
  border: 2px solid #0075A2;
  opacity: 1;
}

button.submit-contact.complete-form:hover{
  -webkit-transform: scale(1.08);
      -ms-transform: scale(1.08);
          transform: scale(1.08);
}

button.submit-contact.complete-form:focus {
  background-color: #fff;
  color: #0075A2;
  border: 2px solid #0075A2;
}

span.visit-site {
  z-index: 3;
  border: 1px solid #2098D1;
  margin-top: 8px;
  color: #ffffff;
  padding: 5px;
  font-family: 'Nunito'
}

.status-msg{
  display: block;
  width: 100%;
  padding: 15px;
  margin-top: 10px;
  text-align: center;
  font-size: 24px;
  font-family: 'Nunito';
  font-weight: 100;
}

.status-msg.show-msg{
  background: rgb(11,181,72);
}

.status-msg.error-msg{
  background: red;
}

.error-msg{
  color: #fff;
}

.show-msg{
  color: white
}

.modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background-color: rgba(0,0,0,.6);
}

@media(max-width: 768px){
  form.form-contact {
    margin: 0;
  }

  .form-wrapper{
    padding: 50px 0;
  }
}

@media(max-width: 640px){
  h3.contact-title{
    font-size: 28px;
  }

  .input-form {
    font-size: 20px;
  }

  .form-wrapper{
    padding: 30px 0;
  }
}

@media(max-width: 480px){
  .form-wrapper{
    padding: 20px 0;
  }
 
  h3.contact-title{
    font-size: 26px;
  }
  
  a.social-item, a.social-item img {
    width: 30px;
    height: 30px;
  }
}


